<template>
  <el-dialog
    title=""
    :visible="dialogVisible"
    width="600px"
    :close-on-click-modal="true"
  >
    <div class="upbox">
      <el-upload
        drag
        multiple
        ref="upload"
        :action="hostUrl + '/file/add'"
        :auto-upload="false"
        :data="userInfo"
        :on-success="dealSuccess"
        :on-error="dealError"
        :before-close="handleClose"
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text"><em></em></div>
      </el-upload>
      <el-button
        style="margin: 15px auto 0"
        size="small"
        type="success"
        @click="submitUpload"
        ></el-button
      >
    </div>
  </el-dialog>
</template>
<script>
export default {
  data() {
    return {
      userInfo: {
        uid: sessionStorage.getItem("uid"),
        path: "",
      },
      fileNum: 0,
      hostUrl: "",
    };
  },
  props: ["dialogVisible"],
  watch: {
    // upPath(oldValue, newValue) {
    //   if (newValue !== oldValue) {
    //     this.userInfo.path = newValue;
    //   }
    // },
  },
  methods: {
    setPath(path) {
      this.userInfo.path = path;
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    dealSuccess() {
      this.$message.success("!");
      this.fileNum += 1;
      if (this.fileNum === this.$refs.upload.uploadFiles.length) {
        this.$emit("reGetFileList", this.userInfo.path);
        this.fileNum = 0;
        this.$refs.upload.clearFiles();
      }
    },
    dealError() {
      this.$message.error(",!");
      this.$emit("reGetFileList", this.userInfo.path);
    },
    handleClose() {
      this.$emit("reGetFileList", this.userInfo.path);
    },
  },
  mounted() {
    let protocol = window.location.protocol; //
    let host = window.location.host; //
    this.hostUrl = `${protocol}//${host}`;
  },
};
</script>
<style scoped>
.upbox {
  width: 100%;
  text-align: center;
}
</style>



